/* barlow-700 - latin */

@font-face {
  font-family: 'Barlow';
  font-style: normal;
  font-weight: 700;
  src: local("Barlow Bold"), local("Barlow-Bold");
  src: url("../fonts/barlow-v5-latin-700.woff2") format("woff2");
  src: url("../fonts/barlow-v5-latin-700.woff") format("woff");
}

.iOS {
  cursor: pointer;
}

body {
  font-family: 'Barlow';
  font-style: normal;
  font-weight: 700;
}